import { Fragment } from 'react';
import {
    Hero,
    Features,
    CharacteristicProducts,
    Banner,
    Categories,
    OffersNearToEnd,
    Opinions,
    CommercialBrands
} from './components';
import { NewsOffers } from '../../components';
import {items} from './data';

export default function Home() {


    return (
        <Fragment>

            <Hero></Hero>

            <Features></Features>

            <NewsOffers items={items} showLabel={true}></NewsOffers>

            <Banner></Banner>

            <CharacteristicProducts></CharacteristicProducts>

            <Categories showFullItem={false}></Categories>

            <OffersNearToEnd></OffersNearToEnd>

            <Categories showFullItem={true}></Categories>

            <NewsOffers items={items} showLabel={true}></NewsOffers>

            <Opinions></Opinions>

            <CommercialBrands></CommercialBrands>

        </Fragment>
    )
}
