import React from 'react'
import { Rating, Timer } from '../../../../../../components'
import AddToCart from '../../../../../../components/AddToCartButton/AddToCart'

export default function ProductCard({ imageName }) {
    return (
        <div style={{'--rating-color': "#FFB800"}} className='col-span-12 h-[420px] relative overflow-hidden lg:col-span-6 mb-5 lg:mb-0 p-1 sm:p-5 flex items-center flex-col lg:flex-row rounded-[10px] border border-[var(--light)]'>

            <img src={require(`../../../../../../../assets/images/${imageName}.png`)} alt='' className='z-[-1] absolute left-0 top-0 w-full h-full object-fill' />

            <div className='absolute right-0 bottom-0 flex justify-between flex-col w-[60%] h-full p-3' style={{ background: 'linear-gradient(180deg, rgba(156, 33, 162, 0.50) 0%, var(--primary) 100%)' }}>

                <div>

                    <h1 className='text-[24px] text-white font-medium mb-1'>برش أي شادو</h1>

                    <div className='card-rating flex items-center'>

                        <Rating />

                        <p className='ms-3 text-[13px] mb-[-3px] text-[#af89b0]'>(4.5)</p>

                    </div>

                </div>

                <div>

                    <button className='bg-white p-2 my-3 w-full flex items-center justify-center rounded-[5px]'>

                        <h6 className='text-[var(--danger-color)] text-[24px] font-bold ms-3'>250.00 رس</h6>

                        <p className='gray-text !text-[#A5A5A5] ms-3' style={{ textDecoration: 'line-through' }}>350.00 ر.س</p>

                    </button>

                    <Timer />

                </div>

                <div className='w-full'>
                    <AddToCart classNames={'w-full !bg-transparent !border !border-white !rounded-[3px]'} />
                </div>

            </div>

        </div>
    )
}
