import React from 'react'

export default function ProductCard(props) {

    const { imageName, classNames } = props;

    return (
        <div className={`${classNames} overflow-hidden slider-hover-card border rounded-tl-[5px] rounded-tr-[5px]`}>

            <div className='overlay-bg absolute top-0 left-0 w-full h-full transition opacity-0 scale-50' style={{ background: 'linear-gradient(180deg, rgba(156, 33, 162, 0.25) 0%, var(--primary) 100%)' }}>

            </div>

            <div className='p-8 flex items-center justify-center relative'>

                <img src={require(`../../../../../assets/images/${imageName}.png`)} className='h-[360px] w-full object-fill' />

            </div>

            <div className='text-center p-5 slider-card-title' style={{ background: 'linear-gradient(180deg, rgba(156, 33, 162, 0.20) 0%, rgba(108, 22, 106, 0.80) 79.17%)' }}>
                <h1 className='text-white font-bold text-[24px] relative z-10'>هاي لايت</h1>
            </div>
        </div >
    )
}
