import React from 'react'
import { SwiperWrapper } from '../../../../components';
import { items } from './data';

export default function CommercialBrands() {
    return (
        <section className='m-section !py-16 top-left-pagination-wrapper custom-pagination-wrapper'>

            <div data-aos="fade-right" className='container'>

                <div className='mt-10 relative top-left-pagination'>

                    <div className='mb-5'>
                        <h2 className='font-bold text-[18px] sm:text-[22px] mb-2'>الماركات التجارية</h2>
                        <p className='gray-text lg:ms-16'>يمكنك التسوق من خلال الماركات التجارية</p>
                    </div>

                    <SwiperWrapper
                        items={items}
                        includeNavigation={true}
                        slidesPerViewCount={[2.5, 3, 5, 6]}
                        autoplayDelay={5000}
                        isLooped={true}
                    ></SwiperWrapper>

                </div>

            </div>

        </section>
    )
}
