
export default function Categories({ showFullItem }) {

    const data = [
        { imageName: "quailty-1", animation: "zoom-in",  classNames: "col-span-6", showFullItem: true },
        { imageName: "quailty-2", animation: "zoom-in-up", classNames: "col-span-6", showFullItem: true },
        { imageName: "quailty-3", animation: "zoom-in-down", classNames: "col-span-12", showFullItem: showFullItem }
    ];

    const renderData = data.map((item, index) => {
        return (
            item.showFullItem
                ?
                <div data-aos={item.animation} key={index} className={`rounded-[10px] overflow-hidden relative h-[250px] flex flex-col justify-center items-center ${item.classNames}`}>

                    <img src={require(`../../../../../assets/images/${item.imageName}.jpeg`)} alt='' className='absolute left-0 top-0 h-full w-full' />

                    {/* Overlay */}
                    <div className="absolute left-0 top-0 w-full h-full bg-[#00000038]"></div>

                    <h1 className='font-medium text-[40px] mb-3 text-white relative z-2'>أفضل جودة</h1>

                    <button className='border border-white text-white bg-transparent p-3 rounded-[5px] relative z-2'>

                        تسوق الان

                    </button>

                </div> :
                null
        )
    })

    return (

        <section data-aos="fade-up" data-aos-anchor-placement="bottom-bottom" className='m-section !mb-0 !py-16 relative' style={{ background: "linear-gradient(90deg, rgba(156, 33, 162, 0.75) -2.01%, rgba(107, 28, 105, 0.98) 85.61%)" }}>

            <div className='grid grid-cols-12 gap-5 container'>

                {renderData}

            </div>

        </section>
    )
}
