import { GloabelProductCard } from "../../../../components";

export const renderProdcuts = ['p-1', 'p-3', 'p-2', 'p-3', 'p-1', 'p-2'].map((product, index) => {
    return <GloabelProductCard key={index} hasDiscount={index % 2 === 0} hasOffer={index % 2 !== 0} classNames={'col-span-12 sm:col-span-6 lg:col-span-4'} imageName={product}></GloabelProductCard>
});
