export default function Hero() {

    return (
        <section className={`relative`}>

            {/* Bg */}
            <div className='h-[1018px] bg-[var(--primary)] absolute left-0 top-0 w-full z-[-1]'></div>

            <div className="relative">

                <div className="container flex  justify-between">

                    <div data-aos="fade-down" className='mt-[350px]'>

                        <div className="text-white">

                            <h1 className="text-[48px] font-medium mb-5">أفضل العروض</h1>

                            <p className="text-[20px] mb-10 lg:max-w-[50%] opacity-70">

                                متجر سلة يوفر لك كل ماتحتاجه من إلكترونيات أو أثاث منزلي
                                إلى أفضل التحفيضات على المنتجات , تسوق الان واستمتع بكل
                                بالتخفيضات على المنتجات

                            </p>

                            <button className="mb-10 border border-white rounded-[10px] text-[20px] py-3 px-32">اكتشف المزيد </button>

                        </div>

                        <img src={require('../../assets/images/pngwing 6.png')} />

                    </div>

                    <div data-aos="fade-right" className='flex h-full relative bg-blue-500 '>

                        <img src={require('../../assets/images/hero-hand.png')} className=' absolute right-[-300px] top-[-170px]' />

                        <img src={require('../../assets/images/hero-women.png')} className='max-h-full' />

                    </div>

                </div>

            </div>

        </section>
    )
}
