import React from 'react'
import { SeactionHeader } from '../../../../components'
import { ProductCard } from './component'

export default function OffersNearToEnd() {
    return (
        <section className='m-section'>

            <div data-aos="fade-up" data-aos-anchor-placement="top-center" className='container'>

                <SeactionHeader href={''} title={'عروض تنتهى قريبا'} paragraph={'تسوق احدث المنتجات المميزة المضافة جديد'} ></SeactionHeader>

                <div className='grid grid-cols-12 my-10 gap-3 lg:gap-10'>

                    <ProductCard imageName="libs-styck" />

                    <ProductCard imageName="monkier" />

                </div>

            </div>

        </section>
    )
}
