import React from 'react'
import {
    carIcon,
    gift,
    goodQuality,
    hours
} from './data'


export default function Features() {
    return (
        <section className='m-section'>

            <div className='container'>

                <div className='grid grid-cols-12 gap-7 rounded-[5px] lg:p-7'>

                    <div data-aos="fade-left" className='col-span-12 lg:col-span-3 border border-[var(--light)] rounded-[5px] justify-center lg:justify-start lg:mb-0 flex items-center p-4'>

                        <img src={goodQuality} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px] text-[var(--light)]'>أفضل جودة</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>منتجات ذات جودة عالية</h5>

                        </div>

                    </div>
                    <div data-aos="flip-left" className='col-span-12 lg:col-span-3 border border-[var(--light)] rounded-[5px] justify-center lg:justify-start lg:mb-0 flex items-center p-4'>

                        <img src={carIcon} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px] text-[var(--light)]'>شحن مجاني</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>شحن مجاني</h5>
                        </div>

                    </div>
                    <div data-aos="flip-right" className='col-span-12 lg:col-span-3 border border-[var(--light)] rounded-[5px] justify-center lg:justify-start lg:mb-0 flex items-center p-4 border-l border-r'>

                        <img src={hours} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px] text-[var(--light)]'>مركز المساعدة 24/7</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>اتصل بنا 24 ساعة يوميا</h5>

                        </div>

                    </div>
                    <div data-aos="fade-right" className='col-span-12 lg:col-span-3 border border-[var(--light)] rounded-[5px] justify-center lg:justify-start lg:mb-0 flex items-center p-4'>

                        <img src={gift} className='me-5' />

                        <div>

                            <h2 className='font-medium mb-1 text-[20px] text-[var(--light)]'>ترويج الهدايا</h2>

                            <h5 className='text-[var(--text-color)] font-medium'>على أوامر أكثر من 50 دولارا</h5>

                        </div>

                    </div>

                </div>

            </div>

        </section>
    )
}
