import { SwiperSlide } from "swiper/react";
import { ProductCard } from "./components";

export const items = ['hay-layt', 'libs-styck', 'foundation', 'perfione', 'pens', 'foundation', 'hay-layt', 'foundation', 'foundation', 'hay-layt', 'foundation',].map((product, index) => {
    return (
        <SwiperSlide key={index}>

            <ProductCard imageName={product} active={index == 1 ? true : false}></ProductCard>

        </SwiperSlide>
    )
});