import React from 'react'
import bg from '../../assets/images/home-banner.jpg';
import { Link } from 'react-router-dom';


export default function Banner() {
    return (

        <div className='m-section !py-20 relative h-[550px] flex items-center'>

            <div className='absolute left-0 top-0 z-[-1] w-full h-full max-w-full max-h-full flex items-center justify-center' style={{ background: `linear-gradient(180deg, rgba(108, 22, 106, 0.00) 0%, var(--primary) 100%)` }}>
            </div>
            <img src={bg} alt='' className='w-full h-full left-0 top-0 object-cover absolute z-[-2]' />

            <div data-aos="fade-right" className='container'>

                <h1 className={`text-white font-bold text-[18px] sm:text-[25px] md:text-[45px] transition mb-5`}>أفضل التخفيضات 2022</h1>

                <p className={`mb-5 text-white text-[14px] lg:text-[20px] lg:max-w-[60%]`}>متجر سلة يوفر لك كل ماتحتاجه من إلكترونيات أو أثاث منزلي بالإضافة إلى أفضل التحفيضات على المنتجات , تسوق الان واستمتع بكل بالتخفيضات على المنتجات</p>

                <Link to={'products'}>
                    <button className='min-btn !bg-transparent border border-white mt-10'>اكتشف المزيد</button>
                </Link>

            </div>

        </div>
    )
}
